<template>
  <div class="historicalRecordBody">
    <div class="user-header">
      <a class="nav-item1" @click="historicalRecordBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="wode">历史</a>
      <a class="history" @click="historyOrder">邮寄记录</a>
    </div>

    <div class="myDollMain">
      <div class="myDollList" v-show="myDollListShow">
        <ul>
          <li v-for="item in history">
            <div class="wawaInfo">
              <img class="wawaImg" :src="item.icon_url"/>
              <div>
                <p class="toptime"><a>{{item.name}}</a><span>{{item.created_at}}</span></p>
                <p class="textovflow">{{item.remark}}</p>
              </div>
            </div>
            <a class="status" v-if="item.pstatus ==1">未申请</a>
            <a class="status" v-if="item.pstatus ==2 && item.ostatus == 1">未发货</a>
            <a class="status" v-if="item.pstatus ==2 && item.ostatus == 4">未发货</a>
            <a class="status" v-if="item.pstatus ==2 && item.ostatus == 2">已发货</a>
            <!--<img class="playHishory" src="../../assets/images/thebar/wh_46.png"/>-->
          </li>
        </ul>
      </div>
      <div class="nomail" v-show="nomailShow">
        <a>~\(*>﹏<*)/~暂无历史!</a>
        <img src="../../assets/images/usercenter/wh_49none.png"/>
      </div>
    </div>

  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        history:[],
        nomailShow:false,
        myDollListShow:true,
      }
    },
    components: {
    },
    mounted:function () {
      var format = function (timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
        return Y+M+D+h+m;
      }
      var _this = this;
      var userID = getCookie("userId")
      $.ajax({
        type: 'POST',
        url: urlhost+"usercenter/historypacks?user_id="+userID,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var status = JSON.parse(response).status;
          console.log(JSON.parse(response))
          if(status == 0){
            _this.nomailShow = true;
            _this.myDollListShow = false;
          }else{
            _this.nomailShow = false;
            _this.myDollListShow = true;
            var mess = JSON.parse(response).message;
            for(var i=0;i< mess.length;i++){
              var num = parseInt(mess[i].created_at)
              var time = format(num);
              mess[i].created_at = time;
            }
            _this.history = mess;
          }

        },
      });
    },
    methods:{
      historicalRecordBack:function () {
        this.$router.go(-1);
      },
      historyOrder:function () {
        this.$router.push({path:"/usercenter/mailOrder"});
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .historicalRecordBody{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    overflow:scroll;
    background-color: #f2f2f2;
  .user-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: n(80);
    background: #fff001;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    border-bottom: 1px solid #d8d8d8;
  .nav-item1{
  .game_1{
    margin-left: n(23);
    width: n(50);
    height: n(50);
  }
  }
  .wode{
    font-size: 14px;
    font-weight: bold;
  }
  .history{
    margin-right: n(23);

  }
  }
  .myDollMain{
    width: 95%;
    margin-top: n(80);
    margin-left: 2.5%;
    overflow: hidden;
  .myDollList{
    margin-top: 10px;
    width: 100%;
  ul{
  li{
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: n(126);
    background: #ffffff;
    padding: 8px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    .status{
      color: red;
    }
  .wawaInfo{
    display: inline-flex;
  .wawaImg{
    width: n(100);
    height: n(100);
  }
  div{
    margin-left: 5px;
    width: n(390);
    height: n(100);
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  .toptime{
    width: n(390);
    height: n(50);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

  }
  .textovflow{
    width: n(390);
    height: n(50);
    overflow:hidden;/*超出长度的文字隐藏*/
    text-overflow:ellipsis;/*文字隐藏以后添加省略号*/
    white-space:nowrap;/*强制不换行*/
  }
  }
  }
  .playHishory{
    width: n(60);
    height: n(60);
  }
  }
  }
  }
    .nomail{
      margin-top: n(80);
      width: 100%;
      height: n(400);
      display: inline-flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      img{
        margin-top: 10px;
        width: n(349);
        height: n(234);
      }
    }
  }
  }
</style>



